@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;900&display=swap);
.navbar {
  justify-content: space-between;
  position: fixed;
  width: 100%;
  top: 0;
  padding: 10px 0px;
  z-index: 1000;
  height: 50px;
  transition: 0.2s;
}
.active-navbar {
  background-color: black;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.356);
}
.logo img {
  position: absolute;
  left: 6rem;
  top:18px;
  -webkit-filter: brightness(2);
          filter: brightness(2);
  display: block;
  width: 130px;
}
.log-out{
  display: flex;
  position: absolute;
  right:-38rem;
}

.row {
  z-index: -1;
  width: 100% !important;
  justify-content: flex-start;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}
.poster {
  margin: 10px 10px;
}
.poster img {
  width: 10rem;
  height: 15rem;
  transition: 0.2s;
  opacity: 0.85;
}
.poster img:hover {
  overflow-y: hidden;
  -webkit-transform: scale(1.12);
          transform: scale(1.12);
  opacity: 1;
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
}
h4 {
  font-size: 20px;
  padding: 0px 10px;
}

.Banner {
  flex-direction: column;
  align-items: flex-start;
  justify-content: left;
  position: relative;
  height: 65vh;
  width: 100%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.Banner::after {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(rgba(255, 255, 255, 0) 10%, rgb(0, 0, 0));
  z-index: 10;
}
.Banner img {
  width: 100%;
  height: 100vh;
}
.banner_content {
  position: relative;
  top: 10%;
  z-index: 100;
}
.title {
  padding: 30px 0px;
  text-align: left;
  width: 100%;
  font-size: 30px;
  font-weight: bold;
  text-shadow: 0px 0px 1px black;
}
.overview {
  text-shadow: 0px 0px 2px black;
  font-size: 15px;
  text-align: left;
  width: 50%;
}
@media (max-width: 600px) {
  .overview {
    width: 80%;
  }
}

.login-page::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url(/netflix-clone/static/media/bg.fa81ed58.jpg);
  background-size: cover;

  z-index: -1;
}
.login-page {
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(
    30deg,
    rgba(0, 0, 0, 0.144),
    rgba(0, 0, 0, 0.932)
  );
}

.login-main > * {
  margin: 11px 0;
}
.login-box {
  font-weight: bold;
  font-size: 50px;
  width: 70vw;
  text-align: center;
}
.login-box-2 {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  font-weight: lighter !important;
}
.login-box-3 {
  position: relative;
  top: 20px;
}
.login-form {
  position: relative;
  top: 20px;
}
.Input {
  color: white;
  background-color: rgba(0, 0, 0, 0.342);
  outline: none;
  border: 1px solid white;
  border-radius: 3px;
  font-size: 20px;
  padding: 0px 0px 0px 10px;
  width: 30vw;
  height: 50px;
}
.login-btn {
  width: 15vw;
  height: 54px;
  background-color: #e50914;
  border: none;
  color: white;
  font: inherit;
  margin-left:4px;
  border-radius: 5px;
  font-size: 20px;
}
.btn-red {
  background-color: #e50914 !important;
  color: white !important;
  font: inherit;
  font-size: 13px !important;
  position: absolute !important;
  top: 15px;
  right: 3vw;
}
@media (max-width: 600px) {
  .login-box {
    margin: auto;
    font-weight: bold;
    font-size: 50px;
    width: 95vw;
    text-align: center;
  }
  .Input {
    padding: 0px 3px;
    width: 70vw;
    height: 50px;
  }
  .login-btn {
    width: 25vw;
    height: 53px;
    background-color: #e50914;
    border: none;
    color: white;
    font: inherit;
    font-size: 15px;
  }
}

.login-main{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.signIn-background {
  background-color: rgba(0, 0, 0, 0.76);
  padding: 40px;
  flex-direction: column;
}
.signUp-input {
  background-color: rgba(143, 142, 142, 0.884) !important;
  color: white !important;
  width: 25vw !important;
  height: 3rem;
  border-radius: 6px !important;
  margin: 20px 0px !important;
}
@media (max-width: 600px) {
  .signUp-input {
    background-color: rgba(143, 142, 142, 0.884) !important;
    color: white !important;
    width: 85vw !important;
    border-radius: 10px !important;
    margin: 10px 0px !important;
  }
}

* {
  margin: 0;
  padding: 0;
}
body {
  font-family: sans-serif;
  background-color: black;
  color: white;
  text-align: center;
  overflow-x: hidden;
}
.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.form {
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

button {
  outline: none;
  cursor: pointer;
}
.red {
  margin: 20px 0px !important;
  background-color: red !important;
  width: 100% !important;
  font-weight: bolder !important;
  color: white !important;
}
.column {
  flex-direction: column;
}
.blue-text {
  color: blue;
  cursor: pointer;
}
.Main {
  text-align: left;
}
.mt2{
  margin:6px 0 ;
}
::-webkit-scrollbar {
  display: none;
}
.btn {
  z-index: 1000;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 20px;
  margin: 0px 10px !important;
}

